<template>
  <div class="content-div">
    <div class="container">
      <div class="row">
        <div class="col-12 col-md-8 my-3">
          <span class="h1 mr-2">COVID-19 Global Diagnosis</span>
        </div>
        <div class="col-12 col-md-4 text-right my-3 updateTime">
          Update : {{allData.updated}} 
        </div>
        <div class="col-12 col-sm-12 col-md-6 col-lg-4 mb-4 move">
          <div class="infoBox" style="border-color:#ffc107">
            <div class="title text-warning">Coronavirus</div>
            <div class="number text-right">{{allData.cases}}</div>
          </div>
        </div>
        <div class="col-12 col-sm-6 col-md-6 col-lg-4 mb-4 move">
          <div class="infoBox" style="border-color:#dc3545">
            <div class="title text-danger">deaths</div>
            <div class="number text-right">{{allData.deaths}}</div>
          </div>
        </div>
        <div class="col-12 col-sm-6 col-md-6 col-lg-4 mb-4 move">
          <div class="infoBox" style="border-color:#40b883">
            <div class="title ">recovered</div>
            <div class="number text-right">{{allData.recovered}}</div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-12 col-md-6">
          <div class="row">
            <div class="col-12 col-sm-12 col-md-12 col-lg-12 mb-4 move">
              <div class="infoBox border-light" >
                <div class="title text-light countyTitle mb-3">ALL</div>
                <div class="row">
                  <div class="col-12">
                    <canvas id="chartAll"></canvas>
                  </div>
                </div>
              </div>  
            </div>
            <div class="col-12 col-sm-12 col-md-12 col-lg-12 mb-4 move">
              <div class="infoBox border-light" >
                <div class="title text-light countyTitle mb-3">{{countryData.country}}</div>
                <div class="row">
                  <div class="col-12 col-lg-12 my-4">
                    <div class="number text-warning" style="font-size: 50px">
                      {{toCurrency(countryData.cases)}}
                      <span class="text-info" style="font-size:20px">+{{toCurrency(countryData.todayCases)}} (Today Cases)</span>
                    </div>
                  </div>
                  <div class="col-12 col-sm-6 col-md-6 mb-4">
                    <div class="subInfoBox border-danger">
                      <div class="title text-danger">deaths</div>
                      <div class="number text-right">{{toCurrency(countryData.deaths)}}</div>
                    </div>
                  </div>
                  <div class="col-12 col-sm-6 col-md-6 mb-4">
                    <div class="subInfoBox">
                      <div class="title">recovered</div>
                      <div class="number text-right">{{toCurrency(countryData.recovered)}}</div>
                    </div>
                  </div>
                  <div class="col-12">
                    <canvas id="chartCountryHistory" width="100%" height="100%" ></canvas>
                  </div>
                </div>
              </div>  
            </div>
            <div class="col-12 col-sm-12 col-md-12 col-lg-12 mb-4 move">
              <div class="infoBox border-light" >
                <div class="title text-light countyTitle mb-3">Top 10</div>
                <div class="row">
                  <div class="col-12">
                    <canvas id="chartCountries" width="100%" height="100%" ></canvas>
                  </div>
                </div>
              </div>  
            </div>
          </div>
        </div>
        <div class="col-12 col-md-6">
          <div class="row">
            <div class="col-12 col-sm-12 col-md-12 col-lg-12 mb-4 move">
              <div class="infoBox border-info" >
                <div class="title text-info">Countries <small style="font-size: 10px" class="float-right text-light">Click to change country</small></div>
                <div class="table-responsive">
                  <table class="table table-sm table-hover table-dark">
                    <thead>
                      <tr>
                        <th>Country</th>
                        <th class="text-right">Cases</th>
                        <th class="text-right">Deaths</th>
                        <th class="text-right">Recovered</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(key,index) in countriesData" :key="key.country" @click="changCountry(index)">
                        <td>{{key.country}}</td>
                        <td align="right">{{toCurrency(key.cases)}}</td>
                        <td align="right">{{toCurrency(key.deaths)}}</td>
                        <td align="right">{{toCurrency(key.recovered)}}</td>
                      </tr>
                    </tbody>  
                  </table>
                </div>
              </div>  
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js';

export default {
  props: {
    
  },
  components: {

  },
  mounted:function(){
    this.getAllData();
    this.getCountriesData();
    // this.getHistory();
    // this.getCountryData("Taiwan");
    // this.getCountryHistory("Taiwan*");
  },
  data(){
    return { 
      allData : "",
      countriesData : "",
      countryData : {
        country:  "Taiwan",
        cases: "0",
        deaths: "0",
        recovered: "0",
        todayCases: "0"
      },
      history:"",
      countryHistory:"",
      histortyChart : null
    }
  },
  methods: {
    getAllData(){
      this.axios.get('https://corona.lmao.ninja/all')
      .then(response=>{
        this.allData = response.data;
        this.changeTime(response.data.updated);
        this.setChartAll(response.data.cases,response.data.deaths,response.data.recovered);
        this.allData.cases = this.toCurrency(response.data.cases);
        this.allData.deaths = this.toCurrency(response.data.deaths);
        this.allData.recovered = this.toCurrency(response.data.recovered);
        // setTimeout(this.getAllData, 60000);
        
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    getCountriesData(){
      this.axios.get('https://corona.lmao.ninja/countries')
      .then(response=>{
        this.countriesData = response.data;

        this.setChartCountries();
        setTimeout(this.getCountriesData, 60000);
        this.getCountryData(this.countryData.country);
        this.getCountryHistory(this.countryData.country);
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    getCountryData(country){
      this.axios.get('https://corona.lmao.ninja/countries/'+country)
      .then(response=>{
        this.countryData = response.data;
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    getHistory(){
      this.axios.get('https://corona.lmao.ninja/historical')
      .then(response=>{
        this.history = response.data;
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    getCountryHistory(country){
      
      if(country == "Taiwan"){
        country = country+ "*";
      }
      this.axios.get('https://corona.lmao.ninja/historical/'+country)
      .then(response=>{
        this.history = response.data;
        this.setChartCountryHistory();
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    changCountry(key){
        this.countryData = this.countriesData[key];
        this.histortyChart.destroy();
        this.getCountryHistory(this.countriesData[key].country);
    },
    changeTime(time){
      var newTime = new Date(time);
      this.allData.updated = newTime;
    },
    toCurrency(num){
      var parts = num.toString().split('.');
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
      return parts.join('.');
    },
    setChartAll(a,b,c){
      var chartAll = document.getElementById('chartAll');
      new Chart(chartAll, {
          type: 'doughnut',
          data: {
              labels: [ 'CORONAVIRUS', 'DEATHS','RECOVERED',],
              datasets: [{
                  label: '# of Votes',
                  data: [a, b,  c],
                  backgroundColor: [
                      'rgba(255, 206, 86, 0.2)',
                      'rgba(255, 99, 132, 0.2)',
                      'rgba(75, 192, 192, 0.2)'
                  ],
                  borderColor: [
                      'rgba(255, 206, 86, 1)',
                      'rgba(255, 99, 132, 1)',
                      'rgba(75, 192, 192, 1)'
                  ],
                  borderWidth: 1
              }]
          },
          options: {
              // scales: {
              //     yAxes: [{
              //         ticks: {
              //             beginAtZero: true
              //         }
              //     }]
              // }
          }
      });
    },
    setChartCountries(){
      var chartCountries = document.getElementById('chartCountries');
      var _countriesData =  this.countriesData;
      new Chart(chartCountries, {
          type: 'horizontalBar',
          // type: 'bar',
          data: {
              labels: [
                _countriesData[0].country,
                _countriesData[1].country,
                _countriesData[2].country,
                _countriesData[3].country,
                _countriesData[4].country,
                _countriesData[5].country,
                _countriesData[6].country,
                _countriesData[7].country,
                _countriesData[8].country,
                _countriesData[9].country
              ],
              datasets: [{
                  label: ['Top 10'],
                  data: [
                    _countriesData[0].cases,
                    _countriesData[1].cases,
                    _countriesData[2].cases,
                    _countriesData[3].cases,
                    _countriesData[4].cases,
                    _countriesData[5].cases,
                    _countriesData[6].cases,
                    _countriesData[7].cases,
                    _countriesData[8].cases,
                    _countriesData[9].cases
                  ],
                  backgroundColor: 'rgba(255, 255, 255, 0.2)',
                  borderColor: 'rgba(255, 255, 255, 1)',
                  borderWidth: 1
              }]
          },
          options: {
              scales: {
                  yAxes: [{
                      ticks: {
                          beginAtZero: true
                      }
                  }]
              }
          }
      });
    },
    setChartCountryHistory(){
      var chart = document.getElementById('chartCountryHistory');
      var timeLabel = [];
      var casesData = [];
      var data = this.history.timeline.cases;
      for(var key in data){
        timeLabel.push(key);
        casesData.push(data[key]);
      }

      this.histortyChart = new Chart(chart, {
          type: 'line',
          data: {
              labels: timeLabel,
              datasets: [{
                  label: this.history.standardizedCountryName,
                  data: casesData,
                  backgroundColor: [
                      'rgba(255, 99, 132, 0)'
                  ],
                  borderColor: [
                      'rgba(255, 99, 132, 1)'
                  ],
                  borderWidth: 2,
              }]
          },
          options: {
              scales: {
                  yAxes: [{
                      ticks: {
                          beginAtZero: true
                      }
                  }]
              }
          }
      });
    }
  }
}
</script>

